import React from 'react';
import './BookBd.css';
import DivAccomodation from '../components/DivAccomodation';
import tent1 from '../images/TENT1.png';
import tent2 from '../images/TENT2.png';
import tent3 from '../images/TENT3.png';
import room1 from '../images/ROOM1.png';
import room2 from '../images/ROOM2.png';

const BookBd = ({onAccomodationChange}) => {
  return(  
    <div className='BookBd flxcenter col-es-12 col-s-12'>
        <div className="col-es-0 col-s-1 m-hidden">
        </div>
        <div className="BookBd-content col-es-12 col-s-10 col-m-12">
            <div className='flxcenter col-es-12 col-s-12'>
                <div className='col-es-0 col-s-0 col-m-1'></div>
                <div className='BookBd-header padding15 col-es-12 col-s-12 col-m-8'>
                    <p className='BookBd-title' > <span className="t1">Accomodation</span> <br /> Select your preferred Accomodation type or choose directly from the accomodations list </p>
                </div>
                <div className='col-es-0 col-s-0 col-m-3'></div>
            </div>  
            <div className="flxcenter col-es-12 col-s-12" style={{padding:5}}>
                <div className="col-es-0 col-s-0 col-m-1">
                </div>
                <div className="flxcenter col-es-12 col-s-12 col-m-8">
                    <DivAccomodation id={'tent1'} name={'Single Tents'} bgimage={tent1} price={1500} onAccomodationChange={onAccomodationChange}/>
                    <DivAccomodation id={'room1'} name={'Single Rooms'} bgimage={room1} price={2000} onAccomodationChange={onAccomodationChange}/>
                    <DivAccomodation id={'tent2'} name={'Double Tents'} bgimage={tent2} price={2500} onAccomodationChange={onAccomodationChange}/>
                    <DivAccomodation id={'room2'} name={'Double Rooms'} bgimage={room2} price={3000} onAccomodationChange={onAccomodationChange}/>
                    <DivAccomodation id={'tent3'} name={'3 Beds Tents'} bgimage={tent3} price={3500} onAccomodationChange={onAccomodationChange}/>
                    <DivAccomodation id={'room3'} name={'3 Beds Rooms'} bgimage={room2} price={4000} onAccomodationChange={onAccomodationChange}/>       
                </div>       
                <div className="col-es-0 col-s-0 col-m-3">
                </div>
            </div>     
        </div>
        <div className="col-es-0 col-s-1 m-hidden">
        </div>   
    </div>


  );
  }


export default BookBd;