import React from 'react';
import './VehiclesB.css';


class VehiclesB extends React.Component{
  //render
  render(){
    const { vehicles } = this.props;
    return(
        <div className='flxcenter GuidesB padding5 col-es-12 col-s-12'>
            <div className='col-es-0 col-s-1 col-m-2'></div>
            <div className='col-es-12 col-s-10 col-m-8'>
                <div className='GuidesBa col-es-12 col-s-12'>
                    <select id="experience" name="experience">
			            <option value="gold">Gold</option>
    			        <option value="silver">Silver</option>
	    		        <option value="bronze">Bronze</option>
		            </select>
		            <select id="language" name="language">
			            <option value="english">English</option>
			            <option value="swahili">Swahili</option>
    			        <option value="italian">Italian</option>
	    		        <option value="french">French</option>
		            </select>
		            <select id="safari" name="safari">
				        <option value="Masai Mara">Masai Mara</option>
		            </select>
                </div>
                <div className='GuidesBb col-es-12 col-s-12'>
                    {
                        vehicles.map(
                            (value,i)=>{
                                return (
                                    <div className='flxcenter col-es-12 col-s-12 border radius5 margin5'>
                                        <div className='flxcenter col-es-12 col-s-12'>
                                            <div className='col-es-4 col-s-4 padding5'>
                                                <img className='Guide-image col-es-12 col-s-12' src={vehicles[i].image} alt='ciao' />
                                            </div>
                                            <div className='flxcenter col-es-8 col-s-8'>
                                                <div className='flxcenter col-es-12 col-s-12 col-m-6'>
                                                    <div className='col-es-6 col-s-6'>Nickname:</div>
                                                    <div className='col-es-6 col-s-6'>{vehicles[i].nickname}</div>
                                                </div>
                                                <div className='flxcenter col-es-12 col-s-12 col-m-6'>
                                                    <div className='col-es-6 col-s-6'>Experience:</div>
                                                    <div className='col-es-6 col-s-6'>{vehicles[i].experience}</div>
                                                </div>
                                                <div className='flxcenter col-es-12 col-s-12 col-m-6'>
                                                    <div className='col-es-6 col-s-6'>Language:</div>
                                                    <div className='col-es-6 col-s-6'>{vehicles[i].language}</div>
                                                </div>
                                                <div className='flxcenter col-es-12 col-s-12 col-m-6'>
                                                    <div className='col-es-6 col-s-6'>Safari:</div>
                                                    <div className='col-es-6 col-s-6'>{vehicles[i].safari}</div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className='col-es-12 col-s-12'>
                                            <button className='col-es-12 col-s-12 bn h40 t4 shadow verde b1blu'>SELECT</button>
                                        </div>                                        
                                    </div>
                                );
                            }
                        )
                    }
                </div>
            </div>
            <div className='col-es-0 col-s-1 col-m-2'></div>
      </div>
    );
  }
}

export default VehiclesB;