import React, { Component } from 'react';
import './App.css';
import {news} from './database/news';
import {guides} from './database/guides';
import {vehicles} from './database/vehicles';

import HomeHa from './containers/HomeHa';
import HomeHb from './containers/HomeHb';
import HomeHc from './containers/HomeHc';

import PagesH from './containers/PagesH';

import HomeBa from './containers/HomeBa';

import BookBa from './containers/BookBa';
import BookBb from './containers/BookBb';
import BookBc from './containers/BookBc';
import BookBd from './containers/BookBd';
import BookBe from './containers/BookBe';

import GuidesB from './containers/GuidesB';

import VehiclesB from './containers/VehiclesB';

import PagesF from './containers/PagesF';

import BookOa from './containers/BookOa';




class App extends Component {
 
//constructor

  constructor(){
    super();
    this.state = {
      route: 'home',
      bookOaDisplay: 'none',
      values: {
        people: 0,
        nights: 0,
        guideexperience: 0,
        vehicletype: 0,
        tent1: 0,
        tent2: 0,
        tent3: 0,
        room1: 0,
        room2: 0,
        room3: 0,
        email:'',
        chiave: 'AIzaSyB0IQNpWvCKsQgtZRBTw_ko7c448fkl'
      },
      costs: {
        guide: 0,
        vehicle: 0,
        accomodation: 0,
        ride: 0,
        other: 0,
      }
    }

  }


//methods
  autocompleta = () => {
    var inputfrom = document.getElementById('from');
    var autocomplete = new window.google.maps.places.Autocomplete(inputfrom);

    var inputto = document.getElementById('to');
    var autocomplete = new window.google.maps.places.Autocomplete(inputto);
  }

  DrivingCostCalculator = () => {
    var origin = document.getElementById('from').value;
    var destination = document.getElementById('to').value;

    var service = new window.google.maps.DistanceMatrixService();

    service.getDistanceMatrix(
        {
            origins: [origin],
            destinations: [destination],
            travelMode: window.google.maps.TravelMode.DRIVING,
            unitSystem: window.google.maps.UnitSystem.METRIC,
            avoidHighways: false,
            avoidTolls: false
        },
        function (response, status) {
            if (status !== window.google.maps.DistanceMatrixStatus.OK) {
                alert('Error: ', status);
            } else {
                document.getElementById("prova").innerHTML = document.getElementById("prova").innerHTML + (response.rows[0].elements[0].distance.value / 1000).toFixed(2);
            }
        }
    );
}

  onRouteChange = (route) => {
    this.setState({route: route});
  }

  onCheckDetails = (bookOaDisplay) => {
    this.setState({bookOaDisplay: bookOaDisplay});
  }

  onNightsChange = () => {
    this.setState(Object.assign(this.state.values, {nights: Number(document.getElementById('nights').value)}));
    this.onSomethingChange();
  }

  onPeopleChange = () => {
    this.setState(Object.assign(this.state.values, {people: Number(document.getElementById('people').value)}));
  }

  onEmailChange = (evt) => {
    this.setState(Object.assign(this.state.values, {email: evt.target.value}));
    console.log(this.state.values.email)
  }

  onGuideexperienceChange = (value) => {
    switch(value){
      case "gold":
        this.setState(Object.assign(this.state.values, {guideexperience: 5000}));
        break;
      case "silver":
        this.setState(Object.assign(this.state.values, {guideexperience: 4000}));
        break;
      case "bronze":
        this.setState(Object.assign(this.state.values, {guideexperience: 3000}));
        break;
      default:
        break;

    }
    this.onSomethingChange();
  }

  onVehicletypeChange = (value) => {
    
    switch(value){
      case "car":
        this.setState(Object.assign(this.state.values, {vehicletype: 4000}));
        break;
      case "jeep":
        this.setState(Object.assign(this.state.values, {vehicletype: 8000}));
        break;
      case "van":
        this.setState(Object.assign(this.state.values, {vehicletype: 10000}));
        break;
      default:
        break;

    }
    this.onSomethingChange();
  }


  onAccomodationChange = () => {
    const tent1 = Number(document.getElementById('tent1').value);
    const tent2 = Number(document.getElementById('tent2').value);
    const tent3 = Number(document.getElementById('tent3').value);
    const room1 = Number(document.getElementById('room1').value);
    const room2 = Number(document.getElementById('room2').value);
    const room3 = Number(document.getElementById('room3').value);
    const accomodationcost = tent1*3000 + tent2*4000 + tent3*5000 + room1*4000 + room2*5000 + room3*6000
    this.setState(Object.assign(this.state.costs, {accomodation: accomodationcost*2}));

  }

  onSomethingChange = () => {
    this.setState(Object.assign(this.state.costs, {guide: this.state.values.guideexperience*this.state.values.nights}));
    this.setState(Object.assign(this.state.costs, {vehicle: this.state.values.vehicletype*(this.state.values.nights+1)}));
  }

  onCheckAvailability = () => {
    fetch('http://localhost:3001/availabilitycheck', {
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        guide: this.state.values.guideexperience,
        vehicle: this.state.values.vehicletype,
        accomodation: this.state.values.tent1
      })
    })
    .then(response => response.json())
    .then(data => alert(data))
  }


//render

  render() {
    return (
      <div >

        {this.state.route === 'home'

        ?

        <div className='home'>
          <div className='HomeH padding5'>
            <HomeHa onRouteChange={this.onRouteChange} />
            <p className="es-t1 s-t1 m-t1" > Go beyond traditional Safaris</p>
            <HomeHb text='We offer the cheapest and the highest quality Safaris. The choice is yours!' onRouteChange={this.onRouteChange}/>
            <div className='col-es-12 col-s-12 h80'></div>
            <HomeHc />
            <div className='col-es-12 col-s-12 h80'></div>
          </div>
          <div className='HomeB'>
            <div className='col-es-12 col-s-12 h80'></div>
            <HomeBa news={news} />
            <div className='col-es-12 col-s-12 h80'></div>
          </div>
          <PagesF />
        </div>


        : 
        
        <div className='pages'>
          <PagesH onRouteChange={this.onRouteChange}/>
        
          {( this.state.route === 'book'

          ?
          <div className='BookB'>
            <div className='col-es-12 col-s-12 h80'></div>
            <BookBa onNightsChange={this.onNightsChange} onPeopleChange={this.onPeopleChange} onPlaceChange={this.autocompleta} onDestinationChange={this.DrivingCostCalculator}/>
            <div className='col-es-12 col-s-12 h80'></div>
            <BookBb text={'Guide'} text1={'Select the desired guide experience or choose directly from the guides list'} onRouteChange={this.onRouteChange} onGuideexperienceChange={this.onGuideexperienceChange} />
            <div className='col-es-12 col-s-12 h80'></div>
            <BookBc text={'Vehicle'} text1={'Select your preferred vehicle type or choose directly from the vehicles list'} onRouteChange={this.onRouteChange} onVehicletypeChange={this.onVehicletypeChange}/>
            <div className='col-es-12 col-s-12 h80'></div>
            <BookBd onAccomodationChange={this.onAccomodationChange}/>
            <div className='col-es-12 col-s-12 h80'></div>
            <BookBe onEmailChange={this.onEmailChange} onCheckAvailability={this.onCheckAvailability} onCheckDetails={this.onCheckDetails} guidecost={this.state.costs.guide} vehiclecost={this.state.costs.vehicle} accomodationcost={this.state.costs.accomodation} people={this.state.values.people} />
            <BookOa onEmailChange={this.onEmailChange} onCheckAvailability={this.onCheckAvailability} bookOaDisplay={this.state.bookOaDisplay} onCheckDetails={this.onCheckDetails} guidecost={this.state.costs.guide} vehiclecost={this.state.costs.vehicle} accomodationcost={this.state.costs.accomodation} people={this.state.values.people}/>          
          </div>
        
          :

            (this.state.route === 'guides'

            ?

            <GuidesB guides={guides}/>

            :
          
            <VehiclesB vehicles={vehicles}/>
          
            )

          )}

          <PagesF />

        </div>


        } 

      </div>  
           
    );
  }
}

export default App;
