import React from 'react';
import './BookOa.css';

const BookOa = ({onEmailChange, onCheckAvailability, bookOaDisplay, onCheckDetails, guidecost, vehiclecost, accomodationcost, people}) => {
  return(
            <div className="sumUpOverlay row-es-12 row-s-12 col-es-12 col-s-12" id="sumUpOverlay" style={{display: bookOaDisplay}}>
                <div className='flxcenter vertical row-es-12 row-s-12'>
                    <div className="out col-es-12 col-s-12 sumUpOverlayoutArea" onClick={()=>onCheckDetails('none')}></div>
                    <div className="flxcenter col-es-12 col-s-12">
                        <div className="out row-es-12 row-s-12 sumUpOverlayoutArea" onClick={()=>onCheckDetails('none')}></div>
                        <div className="sumUpOverlay-content">
                            <p className='t2'> My Safari Recap </p>
                            <table className='col-es-12 col-s-12'>
                                <tr className='h30 col-es-12 col-s-12 margin5'>
                                    <td className='BookBe-M-cell'>Guide:</td>
                                    <td className='BookBe-M-cell'>{guidecost}</td>
                                </tr>
                                <tr className='h30 col-es-12 col-s-12 margin5'>
                                    <td className='BookBe-M-cell'>Vehicle:</td>
                                    <td className='BookBe-M-cell'>{vehiclecost}</td>
                                </tr>
                                <tr className='h30 col-es-12 col-s-12 margin5'>
                                    <td className='BookBe-M-cell'>Accomodation:</td>
                                    <td className='BookBe-M-cell'>{accomodationcost}</td>
                                </tr>
                                <tr className='h30 col-es-12 col-s-12 margin5'>
                                    <td className='BookBe-M-cell'>Ride:</td>
                                    <td className='BookBe-M-cell'></td>
                                </tr>
                                <tr className='h30 col-es-12 col-s-12 margin5'>
                                    <td className='BookBe-M-cell'>Other:</td>
                                    <td className='BookBe-M-cell'></td>
                                </tr>
                                <tr className='h30 col-es-12 col-s-12 margin5'>
                                    <td className='BookBe-M-cell'>Total:</td>
                                    <td className='BookBe-M-cell'>{guidecost + vehiclecost + accomodationcost}</td>
                                </tr>
                                <tr className='h30 t3 col-es-12 col-s-12 margin5'>
                                    <td >Total per person:</td>
                                    <td >{(guidecost + vehiclecost + accomodationcost)/people}</td>
                                </tr>
                                <tr className='BookBe-M-cell h30 t3 col-es-12 col-s-12 margin5'>
                                    <td colSpan={2}><input className='BookBe-M-button col-es-12 col-s-12 h30 t4 radius5' placeholder="Your email or phone number. We'll get back to you." onChange={(evt)=>onEmailChange(evt)} /></td>
                                </tr>
                                <tr className='BookBe-M-cell h30 t3 col-es-12 col-s-12 margin5'>
                                    <td colSpan={2}><button className='BookBe-M-button col-es-12 col-s-12 bn h30 t4 shadow verde b1blu radius5' onClick={()=>onCheckAvailability()}>CHECK FOR AVAILABILITY</button></td>
                                </tr>
                            </table>   
                        </div>
                        <div className="out row-es-12 row-s-12 sumUpOverlayoutArea" onClick={()=>onCheckDetails('none')}></div>
                    </div>
                    <div className="out col-es-12 col-s-12 sumUpOverlayoutArea" onClick={()=>onCheckDetails('none')}></div>
                </div>
            </div>


  );
  }


export default BookOa;