import React from 'react';
import './LbSquare.css';



const LbSquare = ({name, value, bgimage, price, onRouteChange, route, onGuideexperienceChange, onVehicletypeChange}) => {
  return(

        <div className='LbSquare padding5 col-es-6 col-s-6 col-m-3' style={{position: 'relative'}}>
            <input className='LbSquare-input' type="radio" name={name} id={value} value={value} />
            <label className='LbSquare-label h100 w100' for={value} onClick={()=>{onRouteChange(route);onGuideexperienceChange(value); onVehicletypeChange(value);}} style={{backgroundImage: `url(${bgimage})`, position: 'relative'}}>
                <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" version="1.1" id="Layer_1" x="0px" y="0px" viewBox="0 0 426.667 426.667" style={{enableBackground: 'new 0 0 426.667 426.667', position: 'absolute', top: -15, right: -15}} xmlSpace="preserve" width="30px" height="30px" class=""><g><path d="M213.333,0C95.518,0,0,95.514,0,213.333s95.518,213.333,213.333,213.333  c117.828,0,213.333-95.514,213.333-213.333S331.157,0,213.333,0z M174.199,322.918l-93.935-93.931l31.309-31.309l62.626,62.622  l140.894-140.898l31.309,31.309L174.199,322.918z" data-original="#6AC259" class="active-path" data-old_color="#37D678" fill="#37D67F"/></g> 
                </svg>
                
                <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" version="1.1" id="Capa_1" x="0px" y="0px" viewBox="0 0 512 512" style={{enableBackground: 'new 0 0 512 512', position: 'absolute', bottom: -17, right: -35 }} xmlSpace="preserve" width="50px" height="50px">  
                    <g transform="matrix(-1 0 0 1 512 0)">
                        <g>
	                        <g>
		                        <path d="M506.88,244.288l-137.856-128c-2.944-2.752-6.848-4.288-10.88-4.288H16c-8.832,0-16,7.168-16,16v256    c0,8.832,7.168,16,16,16h342.144c4.032,0,7.936-1.536,10.88-4.288l137.856-128c3.264-3.008,5.12-7.264,5.12-11.712    S510.144,247.296,506.88,244.288z" data-original="#000000" class="active-path" data-old_color="#0b996c" fill="#0b996c"/>
                            </g>
                        </g>
                    </g>
                </svg>
                <p className='t6' style={{position: 'absolute', bottom: -15, right: -33, lineHeight: 1}}>{price} <br /> ksh/d</p>
            </label>
            
            
        </div>

    
    );
}


export default LbSquare;
