import React from 'react';
import './BookBc.css';
import LbSquare from '../components/LbSquare';
import LbSquareA from '../components/LbSquareA';
import car from '../images/CAR.png';
import jeep from '../images/JEEP.png';
import van from '../images/VAN.png';
import vehicle from '../images/VEHICLE.png';
import list from '../images/LIST.png';



const BookBc = ({text, text1, onRouteChange, onVehicletypeChange}) => {
  return(
<div className='BookBc flxcenter col-es-12 col-s-12'>
            <div className='col-es-0 col-s-2 m-hidden'></div>
            <div className='BookBc-content col-es-12 col-s-8 col-m-12'>
                <div className='flxcenter col-es-12 col-s-12'>
                    <div className='col-es-0 col-s-0 col-m-1'></div>
                    <div className='BookBc-header padding15 col-es-12 col-s-12 col-m-8'>
                        <p className='BookBc-title' > <span className="t1">{text}</span> <br /> {text1} </p>
                    </div>
                    <div className='col-es-0 col-s-0 col-m-3'></div>
                </div>               
                <div className='flxcenter col-es-12 col-s-12'>
                    <div className='col-es-0 col-s-0 col-m-1'></div>
                    <div className='flxcenter col-es-12 col-s-12 col-m-8'>
                        <LbSquare name={'vehicletype'} value={'car'} bgimage={car} price={10000} route={''} onRouteChange={()=>{}} onGuideexperienceChange={()=>{}} onVehicletypeChange={onVehicletypeChange} />
                        <LbSquare name={'vehicletype'} value={'jeep'} bgimage={jeep} price={15000} route={''} onRouteChange={()=>{}} onGuideexperienceChange={()=>{}} onVehicletypeChange={onVehicletypeChange} />
                        <LbSquare name={'vehicletype'} value={'van'} bgimage={van} price={18000} route={''} onRouteChange={()=>{}} onGuideexperienceChange={()=>{}} onVehicletypeChange={onVehicletypeChange} />
                                  
                    </div>
                    <div className='col-es-0 col-s-0 col-m-3'></div>
                </div>
            </div>
            <div className='col-es-0 col-s-2 m-hidden'></div>
        </div>
    
    );
}


export default BookBc;