import React from 'react';
import './DivAccomodation.css';



const DivAccomodation = ({id, name, bgimage, price, onAccomodationChange}) => {
  return(
        <div className='flxcenter col-es-12 col-s-12 col-m-12'>
            <div className='col-m-2'></div>
            <img className='h50 w50 margin10' src={bgimage} alt={name}></img>
            <div className='t3' style={{flex: 1, textAlign: 'center', lineHeight: 1.5}}>
                Number of {name} <br /> <span className='t4'> {price} Ksh/night </span>
            </div>
            <input id={id} className='h50 w50 radius5' list="numberofrooms" onChange={()=>{onAccomodationChange();}}/>
            <datalist id="numberofrooms">
                <option value="0" defaultValue/>
                <option value="1" />
                <option value="2" />
                <option value="3" />
                <option value="4" />
                <option value="5" />
            </datalist>
            <div className='col-m-2'></div>
        </div>

    
    );
}


export default DivAccomodation;