import React from 'react';
import './BookBe.css';

const BookBe = ({onEmailChange, onCheckAvailability, onCheckDetails, guidecost, vehiclecost, accomodationcost, people}) => {
  return(
        <div className='BookBe'>
            <div className='BookBe-S col-es-12 col-s-12 m-hidden'>
                <button className='bn maxw270 h40 t4 shadow verde b1blu radius5' onClick={()=>onCheckDetails('block')}>CHECK DETAILS</button>
            <div className='col-es-12 col-s-12 h80'></div>
            </div>

            <div className='BookBe-M es-hidden s-hidden m-visible col-m-3 maxw270 padding5'>
                <div className='BookBe-M-div b1blu radius5 col-m-12'>
                    <p className='t2'> My Safari Recap </p>
                    <table className='BookBe-M-table col-m-12'>
                        <tr className='h40 col-m-12'>
                            <td className='BookBe-M-cell'>Guide:</td>
                            <td className='BookBe-M-cell'>{guidecost}</td>
                        </tr>
                        <tr className='h40 col-m-12'>
                            <td className='BookBe-M-cell'>Vehicle:</td>
                            <td className='BookBe-M-cell'>{vehiclecost}</td>
                        </tr>
                        <tr className='h40 col-m-12'>
                            <td className='BookBe-M-cell'>Accomodation:</td>
                            <td className='BookBe-M-cell'>{accomodationcost}</td>
                        </tr>
                        <tr className='h40 col-m-12'>
                            <td className='BookBe-M-cell'>Ride:</td>
                            <td className='BookBe-M-cell' id='prova'></td>
                        </tr>
                        <tr className='h40 col-m-12'>
                            <td className='BookBe-M-cell'>Other:</td>
                            <td className='BookBe-M-cell'></td>
                        </tr>
                        <tr className='h40 col-m-12'>
                            <td className='BookBe-M-cell'>Total:</td>
                            <td className='BookBe-M-cell'>{guidecost + vehiclecost + accomodationcost}</td>
                        </tr>
                        <tr className='h40 t3 col-m-12'>
                            <td >Total per person:</td>
                            <td >{((guidecost + vehiclecost + accomodationcost)/people).toFixed(2)}</td>
                        </tr>
                        <tr className='h40 t3 col-m-12'>
                            <td colSpan={2}><input className='BookBe-M-button col-m-12 h40 t4 radius5' placeholder="Your email or phone number. We'll get back to you." onChange={(evt)=>onEmailChange(evt)}/></td>
                        </tr>
                        <tr className='h40 t3 col-m-12'>
                            <td colSpan={2}><button className='BookBe-M-button col-m-12 bn h40 t4 shadow verde b1blu radius5' onClick={()=>onCheckAvailability()}>CHECK FOR AVAILABILITY</button></td>
                        </tr>
                    </table>                  
                </div> 
            </div>
        </div>  
    


  );
  }


export default BookBe;